.formWrapper {
    max-width: 700px;
    margin: 0 auto;
    padding: 20px;
    height: auto;
    position: relative;
  }
  
  .row {
    display: flex;
    gap: 20px;
    margin-bottom: 5px;
  }
  
  .row > div {
    flex: 1;
  }
  
  input,
  select,
  button {
    width: 100%;
    padding: 10px;
    margin: 5px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .error {
    color: var(--danger);
    font-size: 12px;
  }
  
  .image-preview {
    margin-top: 10px;
  }
  
  button {
    background-color: var(--social);
    color: white;
    border: none;
    cursor: pointer;
    padding: 10px;
    border-radius: 4px;
  }
  
  .pesticidePictureLabel{
    background-color: var(--lightSocial);
    border:2px solid var(--midSocial);
    color: white;
    font-size: 12px;
    width:fit-content;
    font-weight: 300;
    border-radius: 5px;
    padding: 10px 20px;
    max-height: 40px;
  }
  .pesticidePicture{
    display: none;
  }
  
  .cancel-button {
    background-color: var(--danger);
  }
  
  /* Adjust for screens with smaller width */
  @media (max-width: 768px) {
    .row {
      flex-direction: column;
    }
  
    .row > div {
      flex: none;
      width: 100%;
    }
  }
  .pesticidePictureLabel{

  }
  