@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

:root {
  /* --bg1:#1877f2; */
  --bg1:#28544B;
  --bg2:#fff;
  --bg3:#ededed;
  --gold:gold;
  --cardColor:#151515;
  --social:#007bff;
  --lightSocial:#b3d3f5;
  --midSocial:#2464a8;
  --danger:#dc3545;
  --yellow:#ffc107;
  --green:#28544B;
  --lightDanger:#dc7680;
  --lightGreen:#96f3e0;
  --midDanger:#b83e4a;
  --midGreen:#339b86;

  --t1:#fff;
  --t2:#000;
  --t3:rgb(177, 175, 175);
}
*{
  margin: 0;
  padding: 0;
  font-family: 'Poppins';
  scroll-behavior: smooth;
  box-sizing: border-box;
  /* overflow-x: hidden; */
}
.swiperWrapper{
  margin:150px 10px 100px 10px;
  font-size:20px;

}

.heading{
  text-align: center;
  margin-bottom: 80px;
  color: var(--yellow);
  border-bottom: 2px solid lightgray;
  width: 500px;
  left: 50%;
  transform: translate(-50%);
  position: relative;
}

.formContainer{
  height: 100vh;
  display: flex;
  background-color: var(--bg3);
  justify-content: center;
  align-items: center;
}
.formBox{
  background-color: var(--bg2);
  padding: 20px ;
  border-radius:10px ;
  position: relative;
}

.inputContainer{
  height: 70px;
}
.inputContainer p{
  color: red;
  font-size: 10px;

}
input{
  padding: 15px 20px;
  border-radius: 5px;
  border:2px solid var(--bg3);
  min-width: 300px;
}
input:focus{
  outline: 2px solid var(--bg1);

}
form input[type="submit"]{
  background-color: var(--bg1);
  border: 0;
  color: var(--t1);
  font-size: 14px;
  margin-bottom: 10px;
  position: relative;
  top: 20px;

}
 .pageChange {
  display: flex;
  flex-direction: column;
  font-size: 12px;
  color: var(--t3);
  margin-top: 10px;
  justify-content: center;
  text-align: center;
  position: absolute;
  bottom: 10px;
  min-width: 300px;
}

.pageChange a{
  text-align: center;
  text-decoration: none;
  color: var(--t1);
  background-color: #01c851;
  padding: 15px;
  border-radius: 5px;
  font-size: 14px;

  
}
@media (max-width:400px){
  .formContainer{
      background-color: transparent;
      border-radius: 10px;
  }
  .formBox{
      width: 100%;
      box-shadow: 0;
  }
  .formBox .inputContainer{
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .inputContainer input{
    padding: 15px;
  }
  form input[type="submit"]{
    width: 100%;
    margin-top: 15px;
  }
  .pageChange{
    width: 95%;
    left: 50%;
    transform: translate(-50%);
  }


  
}
